import axios from '../utils/axios';

class EarnChopRuleService {
  list = async () => {
    const earnChopRules = await axios.get('/api/v1/earnChopRules');
    return earnChopRules;
  };

  create = async (data) => {
    const earnChopRule = await axios.post('/api/v1/earnChopRules', data);
    return earnChopRule;
  };

  get = async (id) => {
    const earnChopRule = await axios.get(`/api/v1/earnChopRules/${id}`);
    return earnChopRule;
  };

  update = async (id, data) => {
    const earnChopRule = await axios.patch(`/api/v1/earnChopRules/${id}`, data);
    return earnChopRule;
  };

  delete = async (id) => {
    const earnChopRule = await axios.delete(`/api/v1/earnChopRules/${id}`);
    return earnChopRule;
  };
}

const earnChopRuleService = new EarnChopRuleService();

export default earnChopRuleService;
