import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { DateTimePicker } from '@material-ui/pickers';
import { Alert } from '@material-ui/lab';
import { paymentTypes } from '../../../constants/paymentTypes';
import { ruleTypes } from '../../../constants/ruleTypes';
import { useTranslation } from 'react-i18next';
import earnChopRuleService from '../../../services/earnChopRuleService';
import moment from 'moment-timezone';

const useStyles = makeStyles(() => ({
  root: {}
}));

function EarnChopRuleEditForm({ className, earnChopRule, ranks, ...rest }) {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  return (
    <Formik
      initialValues={{
        name: earnChopRule.name || '',
        description: earnChopRule.description || '',
        payment_type: earnChopRule.payment_type || 'ALL',
        type: earnChopRule.type || 'AMOUNT',
        rule_unit: earnChopRule.rule_unit || 1,
        rule_chops: earnChopRule.rule_chops || 1,
        rank_id: earnChopRule.rank_id || '',
        activated_at: moment(earnChopRule.activated_at ? earnChopRule.activated_at : '').format(
          'YYYY-MM-DD HH:mm:ss'
        ),
        expired_at: moment(earnChopRule.expired_at ? earnChopRule.expired_at : '').format(
          'YYYY-MM-DD HH:mm:ss'
        )
      }}
      validationSchema={Yup.object().shape({
        name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') })),
        description: Yup.string().max(255),
        payment_type: Yup.string().oneOf(['ALL', 'LINE_PAY', 'CASH']),
        type: Yup.string().oneOf(['AMOUNT', 'ITEM_COUNT']),
        rule_unit: Yup.number().min(1),
        rule_chops: Yup.number().min(0)
      })}
      onSubmit={async (values, { resetForm, setErrors, setStatus, setSubmitting }) => {
        try {
          console.log(values);
          await await earnChopRuleService.update(earnChopRule.id, {
            ...values,
            id: earnChopRule.id
          });
          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar(t('Updated', { name: t('Earn Chop Rule') }), {
            variant: 'success'
          });
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
        setFieldValue
      }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label={t('Name')}
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.description && errors.description)}
                    fullWidth
                    helperText={touched.description && errors.description}
                    label={t('Description')}
                    name="description"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.description}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Rank')}
                    name="rank_id"
                    onChange={handleChange}
                    select
                    required
                    SelectProps={{ native: true }}
                    value={values.rank_id}
                    variant="outlined"
                  >
                    {ranks.map((rank) => (
                      <option key={rank.id} value={rank.id}>
                        {rank.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Payment Type')}
                    name="payment_type"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.payment_type}
                    variant="outlined"
                  >
                    {paymentTypes.map((paymentType) => (
                      <option key={paymentType} value={paymentType}>
                        {t(`PaymentType.${paymentType}`)}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Type')}
                    name="type"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.type}
                    variant="outlined"
                  >
                    {ruleTypes.map((ruleType) => (
                      <option key={ruleType} value={ruleType}>
                        {t(`Type.${ruleType}`)}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={12} xs={12} />
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.rule_unit && errors.rule_unit)}
                    fullWidth
                    helperText={touched.rule_unit && errors.rule_unit}
                    label={t('Rule Unit')}
                    name="rule_unit"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.rule_unit}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.rule_chops && errors.rule_chops)}
                    fullWidth
                    helperText={touched.rule_chops && errors.rule_chops}
                    label={t('Rule Chops')}
                    name="rule_chops"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.rule_chops}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12} />
                <Grid item md={6} xs={12}>
                  <DateTimePicker
                    error={Boolean(touched.activated_at && errors.activated_at)}
                    fullWidth
                    helperText={touched.activated_at && errors.activated_at}
                    value={values.activated_at}
                    label={t('Activated At')}
                    name="activated_at"
                    onBlur={handleBlur}
                    format="YYYY-MM-DD HH:mm:ss"
                    required
                    onChange={(date) =>
                      setFieldValue('activated_at', moment(date).format('YYYY-MM-DD HH:mm:ss'))
                    }
                    inputVariant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <DateTimePicker
                    error={Boolean(touched.expired_at && errors.expired_at)}
                    fullWidth
                    helperText={touched.expired_at && errors.expired_at}
                    value={values.expired_at}
                    label={t('Expired At')}
                    name="expired_at"
                    onBlur={handleBlur}
                    format="YYYY-MM-DD HH:mm:ss"
                    required
                    onChange={(date) =>
                      setFieldValue('expired_at', moment(date).format('YYYY-MM-DD HH:mm:ss'))
                    }
                    inputVariant="outlined"
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

EarnChopRuleEditForm.propTypes = {
  className: PropTypes.string,
  earnChopRule: PropTypes.object.isRequired,
  ranks: PropTypes.array.isRequired
};

export default EarnChopRuleEditForm;
