import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useRank from '../../../hooks/useRank';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import EarnChopRuleEditForm from './EarnChopRuleEditForm';
import Header from './Header';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import earnChopRuleService from '../../../services/earnChopRuleService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function EarnChopRuleEditView() {
  const { id } = useParams();
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [earnChopRule, setEarnChopRule] = React.useState();
  const { ranks } = useRank();
  const { t } = useTranslation();

  const getEarnChopRule = useCallback(async () => {
    const data = await earnChopRuleService.get(id);
    setEarnChopRule(data.data.data);
  }, [isMountedRef]);

  useEffect(() => {
    getEarnChopRule();
  }, [getEarnChopRule]);

  if (!earnChopRule) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Edit Earn Chop Rules')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <EarnChopRuleEditForm earnChopRule={earnChopRule} ranks={ranks} />
        </Box>
      </Container>
    </Page>
  );
}

export default EarnChopRuleEditView;
